.socials {
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: rem($postsArticleSocialsRowGap);
  margin-bottom: rem($postsArticleSocialsBotM);
  @include e(profile) {
    @include getCssVar(background, black);
    & a {
      text-decoration: none;
    }
    @include m(inner) {
      display: grid;
      grid-template-columns: max-content repeat(2, auto);
      grid-column-gap: rem($postsArticleSocialsInnerColGap);
      place-items: center;

      @include getCssVar(color, white);
      @include m(title) {
        @include setTextProperties(sans, regular, $baseFontSize);
        transition: all ease 0.31s;
        justify-self: start;
      }
      @include m(count) {
        @include setTextProperties(sans, regular, $postsArticleSocialsCountFontSize);
        transition: all ease 0.31s;
        padding-right: rem($postsArticleSocialsCountRightP);
        justify-self: end;
      }
    }
  }
}
