  }
  &:nth-of-type(2) {
    grid-area: 3 / 1 / 4 / 2;
  }
  &:nth-of-type(3) {
    grid-area: 3 / 2 / 4 / 3;
  }
  &:nth-of-type(4) {
    grid-area: 8 / 1 / 9 / 3;
  }
  &:nth-of-type(5) {
    grid-area: 10 / 1 / 11 / 3;
  }
  &:nth-of-type(7) {
    grid-area: 3 / 3 / 4 / 4;
  }
  &:nth-of-type(8) {
    grid-area: 6 / 3 / 7 / 4;
  }
  &:nth-of-type(9) {
