.most-viewed-games {
  margin-bottom: rem($postsArticlePopularBotM);
  & .post {
    @include e(image-container) {
      margin-bottom: rem($postsArticlePopularImageBotM);
    }
    @include e(main) {
      margin-bottom: rem($postsArticlePopularMainBotM);
      @include m(info) {
        column-gap: unset;
      }

      @include m(title) {
        font-size: rem($postsArticlePopularMainTitleFontSize);
      }
    }
    @include m(small) {
      grid-template-columns: 0.39fr 0.61fr;
    }
  }
}
