@import "./vars";
@import "./topheader";
@import "./menu";
@import "./control-group";
@import "./logo";
@import "./sidebar";

.header {
  padding: 0 rem($headerSidesP);
  width: 100%;
  background-color: transparent;
  position: absolute;
  z-index: 20;
  display: grid;
  place-items: center;
}

.header-navbar__sticky {
  transition: all ease 0.31s;
  position: fixed;
  top: 0;
  justify-content: space-around;
  @include getCssVar(background-color, header-menu-background);
}
