.topheader {
  width: 100%;

  .contact {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: 1fr;
    justify-content: space-between;
    padding: rem($topheaderTopP) 0 rem($topheaderBotP);
    border-bottom: $baseBorder solid getInlineCssVar(20white);

    @include e(registration) {
      grid-area: 1 / 1 / 2 / 2;
      display: grid;
      grid-template-columns: repeat(2, auto);
      column-gap: rem($topheaderRegistrationGap);

      & i {
        @include getCssVar(color, red);
      }

      @include m(block) {
        display: grid;
        grid-template-columns: repeat(3, auto);
        place-items: center;
        column-gap: rem($topheaderRegistrationBlockGap);
      }
      @include m(text) {
        @include setTextProperties(rubik, regular, $topheaderRegistrationBlockTextSize);
        @include getCssVar(color, white);
      }
      @include m(link) {
        @include getCssVar(color, grey);
        @include setTextProperties(rubik, regular, $topheaderRegistrationBlockTextSize);
        text-decoration: none;
        transition: all ease 0.31s;
        &:hover {
          @include getCssVar(color, white);
        }
      }
    }
  }
}
