.footer__heading {
  @include unsetAll((padding, margin));
  padding-bottom: rem($footerHeadingBotP);
  margin: rem($footerHeadingTopM) 0 rem($footerHeadingBotM);
  @include setTextProperties(rubik, medium, $footerHeadingFontSize, 1.4);
  @include getCssVar(color, white);

  &:after {
    @include beforeAfterConstructorRect(15%, rem($footerHeadingAfterHeight)) {
      @include getCssVar(background-color, red);
      display: block;
      bottom: 0;
    }
  }
}
