.header__navbar {
  width: 100%;
  position: static;
}

.menu {
  @include e(list) {
    display: grid;
    grid-template-columns: repeat(7, max-content);

    @include m(item) {
      border: unset;
      padding: rem($headerMenuTopBotP) rem($headerMenuSidesP);
      transition: all ease 0.31s;
      @include setTextProperties(rubik, medium, $headerMenuItemFontSize, 22px);
      @include getCssVar(color, white);
      display: grid;
      grid-template-columns: repeat(2, auto);

      &:nth-of-type(1) {
        border: unset;
      }

      &:before {
        @include beforeAfterConstructorRect(100%, rem($headerHoverUnderlineH)) {
          content: "";
          @include getCssVar(background-color, red);
          transition: 0.5s;
          transform: scaleX(0);
          @include positioning(100%, 0);
          z-index: 1;
        }
      }

      &:hover {
        @include getCssVar(color, red);
        &:before {
          transform: scaleX(1);
        }
      }
    }
  }

  @include e(sub-menu) {
    @include m(list) {
      position: absolute;
      z-index: 1;
      @include getCssVar(background, white);
      width: rem($headerSubmenuW);
      padding: rem($headerSubmenuTopBotP) 0;
      @include positioning(103%, 0);
      transition: all ease 0.31s;
    }

    @include m(item) {
      padding-right: rem($headerSubmenuItemRightP);
      position: relative;
      display: grid;
      grid-template-columns: repeat(2, auto);
      @include getCssVar(color, secondary-text);

      & > a {
        @include getCssVar(color, light-grey);
        transition: 0.5s;
        &:hover {
          @include getCssVar(color, red);
        }
      }
    }
  }
}

.sec-sub-menu-list {
  left: 100%;
  top: 0;
}

.after-caret {
  &::after {
    content: "\f107";
    @include setTextProperties(awesome, semi-bold, 12px);
    transform: rotate(0deg);
    padding: 0 rem($headerMenuAfterCaretSidesP);
    transition: all ease 0.31s;
  }

  @include e(submenu) {
    &::after {
      content: "\203A";
      @include setTextProperties(awesome, semi-bold, 12px);
      transform: rotate(0deg);
      padding: 0 rem($headerMenuAfterCaretSidesP);
      justify-self: end;
    }
  }

  @include m(hover) {
    &::after {
      transform: rotate(180deg);
    }
  }
}
