.header-sidebar {
  position: fixed;
  @include positioning(0, 0);
  transition: all ease 0.8s;
  padding: rem($headerSidebarPs);
  height: 100vh;
  max-width: rem($headerSidebarMaxW);
  overflow-y: scroll;
  @include getCssVar(background-color, black);
  @include getCssVar(color, white);

  @include e(heading) {
    @include setTextProperties(rubik, medium, $headerSidebarHeadingsFontSize, 1.5);
  }

  @include e(description) {
    @include setTextProperties(rubik, regular, $baseFontSize);
  }

  @include e(address) {
    font-style: normal;
    & i {
      @include getCssVar(color, red);
      margin-right: rem($headerSidebarIconRightM);
    }
    @include setTextProperties(rubik, regular, $headerSidebarAddressFontSize);
    @include m(email) {
      & a {
        color: inherit;
        text-decoration: none;
      }
    }

    @include m(phone) {
      & a {
        color: inherit;
        text-decoration: none;
      }
    }
  }

  @include e(title) {
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    @include m(logo) {
      max-width: rem($headerSidebarLogoMaxSize);
    }
    @include m(close) {
      width: rem($headerSidebarCloseButS);
      aspect-ratio: 1 / 1;
      cursor: pointer;
      @include getCssVar(background-color, red);
      transition: all 0.4s ease;
      & i {
        @include getCssVar(color, white);
        @include setTextProperties(awesome, black-bold, $headerSocialIconSize, 1);
      }
      &:hover {
        @include getCssVar(background-color, orange);
      }
    }
  }
}

.sidebar-hidden {
  left: -40%;
}
