.posts__heading {
  height: fit-content;
  align-self: end;
  &:nth-of-type(1) {
    grid-area: 1 / 1 / 2 / 3;
  }
  &:nth-of-type(2) {
    grid-area: 6 / 1 / 7 / 2;
  }
  &:nth-of-type(3) {
    grid-area: 6 / 2 / 7 / 3;
  }
  &:nth-of-type(4) {
    grid-area: 11 / 1 / 12 / 3;
  }
  &:nth-of-type(5) {
    grid-area: 13 / 1 / 14 / 3;
  }
  &:nth-of-type(7) {
    grid-area: 4 / 3 / 5 / 4;
  }
  &:nth-of-type(8) {
    grid-area: 8 / 3 / 9 / 4;
  }
  &:nth-of-type(9) {
    grid-area: 11 / 3 / 12 / 4;
  }
}
