@import "./vars";

.footer .social-icon {
  grid-template-columns: repeat(auto-fill, $smallIconW);
  row-gap: rem($smallIconRowGap);
}

.header .h-socials {
  margin: unset;
  @include e(list) {
    display: grid;
    grid-template-columns: repeat(5, auto);
    column-gap: rem($headerIconGap);
    list-style: none;
    @include unsetAll((padding, margin));

    @include m(item) {
      & > a {
        padding: rem($headerIconTopBotP) rem($headerIconSidesP);
        @include setTextProperties(null, null, 15px, 1);
        @include getCssVar(color, grey);
        text-decoration: none;
        transition: all ease 0.31s;
      }
      &:hover > a {
        @include getCssVar(color, white);
      }
    }
  }
}

.header-sidebar .h-socials {
  @include e(list) {
    justify-content: start;
    column-gap: rem($headerSidebarGap);
    @include m(item) {
      & > a {
        padding: unset;
        @include getCssVar(color, white);
        @include getCssVar(background-color, light-grey);
        width: rem($headerSidebarSize);
        aspect-ratio: 1 / 1;
        border-radius: 100%;
        display: grid;
        place-items: center;
      }
      &:hover > a {
        @include getCssVar(background-color, red);
      }
    }
  }
}
