@import "./vars";
@import "./heading";
@import "./general-post/index";
@import "./articles/index";
@import "./control-group";

.posts {
  padding: rem($postsFeaturedPs);
  @include getCssVar(background-color, dark-green);
  @include e(container) {
    grid-auto-rows: auto;
    display: grid;
    grid-row-gap: rem($postsFeaturedGaps);
  }
}

.post-card__padding {
  padding: rem($postsCardsTopBotP) rem($postsCardsSidesP);
}
