@import "./vars";
@import "./menu";
@import "./logo";
@import "./search-form";

.header {
  @include setTextProperties(rubik, medium, $baseFontSize);
  @include getCssVar(background-color, white);
  padding: rem($headerTopBotP) rem($headerSidesP);
}

.header-navbar__sticky {
  transition: all ease 0.31s;
  position: fixed;
  @include positioning(0, 0);
  width: 100%;
  @include getCssVar(background-color, white);
  z-index: 10;
  padding: rem($headerTopBotP) rem($headerSidesP);
  & .navbar__menu {
    @include positioning(100%, 0);
  }
}
