.latest-games {
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: rem($postsArticleLatestRowGap);
  margin-bottom: rem($postsArticleLatestBotM);
  & .post {
    @include e(main-tag) {
      margin-top: rem($postsArticleLatestTagTopM);
      margin-bottom: rem($postsArticleLatestTagBotM);
    }
    @include e(image-container) {
      margin-bottom: rem($postsArticleLatestImageBotM);
    }
  }
  & .button-style-1 i {
    @include getCssVar(font-weight, semi-bold);
    @include getCssVar(color, white);
    margin-right: unset;
  }
}
