.slider-item {
  padding: rem($newsTopP) 0 rem($newsBotP);
  position: relative;
  @include getCssVar(color, white);
  & i {
    @include getCssVar(color, red);
  }
  @include e(inner) {
    min-height: rem($newsSectionMinHeight * 0.75472);
    padding: 0 rem($newsSidesInnerP);
    position: relative;
    transform: translateY(50px);
    transition: all 0.8s ease;
    transition-delay: 0.5s;
    opacity: 0;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(6, auto);
    @include m(tag) {
      display: block;
      height: rem($newsTagHeight);
      max-height: rem($newsTagHeight);
      margin-bottom: rem($newsBigM);
      grid-area: 1 / 1 / 2 / 3;
      & > a {
        @include getCssVar(background-color, purple);
        @include setTextProperties(rubik, regular, $newsTagFontSize);
        @include getCssVar(color, white);
        text-decoration: none;
        padding: 0 rem($newsTagRightP) 0 rem($newsTagLeftP);
        clip-path: polygon(100% 0%, 90% 50%, 100% 100%, 0 100%, 0% 50%, 0 0);
        height: 100%;
        display: grid;
        place-content: center;
        width: fit-content;
      }
    }
    @include m(date) {
      margin-bottom: rem($newsSmallM);
      grid-area: 2 / 1 / 3 / 3;
      @include setTextProperties(sans, regular, $newsDateFontSize);
      & > i {
        @include setTextProperties(awesome, regular, $newsDateIconFontSize);
        margin-right: rem($newsIconsM);
      }
    }
    @include m(title) {
      @include setTextProperties(rubik, medium, $newsTitleFontSize, 1.2);
      grid-area: 3 / 1 / 4 / 3;
      margin: 0 0 rem($newsTitleBotM);
    }
    @include m(description) {
      @include setTextProperties(sans, regular, $baseFontSize);
      grid-area: 4 / 1 / 5 / 3;
      margin: 0 0 rem($newsDescBotM);
    }
  }
}

.inner__info {
  list-style: none;
  padding: 0;
  margin: 0 0 rem($newsSmallM);
  grid-area: 5 / 1 / 7 / 3;

  @include setFlex(row, start, center);
  flex-wrap: wrap;
  @include setTextProperties(sans, regular, $newsInnerInfoFontSize);
  & > * > i {
    margin-right: rem($newsIconsM);
  }
  @include m(user) {
    margin-right: rem($newsIconRightM);
    & a {
      @include getCssVar(color, white);
      @include getCssVar(font-weight, medium);
      text-decoration: none;
      transition: all ease 0.31s;
    }
    & a:hover {
      @include getCssVar(color, red);
    }
  }
  @include m(comments) {
    margin-right: rem($newsIconRightM);
    & a {
      @include getCssVar(color, white);
      @include getCssVar(font-weight, medium);
      text-decoration: none;
      transition: all ease 0.31s;
    }
    & a:hover {
      @include getCssVar(color, red);
    }
  }

  @include m(time) {
    margin-right: rem($newsIconRightM);
  }
}

.slick-current .slider-item__inner {
  transform: translateY(0);
  opacity: 1;
}
