.footer .email-subs {
  @include unsetAll((background-color, padding));
  display: block;
  margin: rem($footerEmailSubsTopM) 0 rem($footerEmailSubsBotM);

  @include e(input) {
    grid-template-columns: auto max-content;
    grid-row: 1fr;

    @include m(email) {
      min-height: 100%;
      @include getCssVar(background-color, light-grey);
      @include getCssVar(color, white);
      padding-left: rem($footerEmailSubsInputLeftP);
      padding-right: rem($footerEmailSubsInputRightP);
      text-align: start;
      border: unset;
    }

    @include m(submit) {
      padding: rem($footerEmailSubsSubmitTopBotP) rem($footerEmailSubsSubmitSidesP);
      @include setTextProperties(rubik, regular, $baseFontSize);
      @include getCssVar(background-color, red);
      @include getCssVar(color, white);
      border: unset;
      min-width: rem($footerEmailSubsSubmitMinW);
      aspect-ratio: 1 / 1;
    }
  }
}
