.categories {
  margin-bottom: rem($postsArticleCategoriesBotM);
}

.recent-games,
.popular-games {
  & .post-small {
    grid-template-columns: $postsCategoriesImageMaxW 1fr;
    grid-auto-columns: auto;
    row-gap: 0.25rem;

    @include e(image-container) {
      width: 100%;
    }
  }
  display: none;
  transition: all ease 0.5s;
  position: relative;
  top: 10%;
  opacity: 0;

  @include m(active) {
    display: block;
    top: 0;
    opacity: 1;
    animation: slideaway 1e3ms;
  }
}
