.recent-posts {
  margin-bottom: rem($postsArticleRecentBotM);
  & .post {
    @include m(small) {
      grid-template-columns: repeat(2, auto);
      grid-row-gap: rem($postsArticleRecentPostRowGap);
    }

    @include e(description) {
      @include m(date) {
        grid-area: 1 / 1/ 2/ 2;
      }
      @include m(title) {
        grid-area: 2 / 1 / 3 / 2;
      }
      @include m(chevron) {
        grid-area: 1 / 2 / 3 / 3;
        align-self: center;
        padding: rem($postsArticleRecentChevronTopBotP)
          rem($postsArticleRecentChevronSidesP);
        @include getCssVar(color, white);
      }
    }
  }
}
