.header-buttons {
  display: grid;
  grid-template-columns: repeat(3, auto);
  place-items: center;
  column-gap: rem($headerControlGroupGap);

  &__shopping,
  &__search {
    @include setTextProperties(awesome, black, $headerControlButIconSize, 1);
    & i {
      @include getCssVar(color, white);
      transition: all ease 0.31s;
    }
    &:hover i {
      @include getCssVar(color, red);
    }
  }

  @include e(menu) {
    @include m(toggler) {
      height: fit-content;
      display: block;
      cursor: pointer;
      @include m(bar) {
        display: block;
        width: rem($headerTogglerBarW);
        height: rem($headerTogglerBarH);
        background-color: white;
        &:not(:nth-last-of-type(1)) {
          margin-bottom: rem($headerTogglerBarBotM);
        }
      }
    }
  }
}
