.featured-games > * {
  & > .post .featured-games__image {
    @include basicBackground {
      aspect-ratio: 1 / 1;
      margin: unset;
    }
  }

  & .post__main-background {
    background: image("featured-main-post.jpg");
    @include basicBackground {
      width: 100%;
      height: 100%;
      position: absolute;
      @include positioning(0, 0);
      filter: brightness(60%);
      z-index: 1;
    }
  }

  & > .post:nth-of-type(2) .featured-games__image {
    background: image("featured-soldier.jpg");
    @include basicBackground;
  }

  & > .post:nth-of-type(3) .featured-games__image {
    background: image("featured-soldier2.jpg");
    @include basicBackground;
  }

  & > .post:nth-of-type(4) .featured-games__image {
    background: image("featured-soldier3.jpg");
    @include basicBackground;
  }
}

.racing-games {
  @include e(image) {
    aspect-ratio: 1.35 / 1;
  }
  & > *:nth-child(1) &__image {
    background: image("featured-soldier.jpg");
    @include basicBackground {
      aspect-ratio: 1.7 / 1;
    }
  }

  & > *:nth-child(2) &__image {
    background: image("racing-games-1.jpg");
    @include basicBackground;
  }

  & > *:nth-child(3) &__image {
    background: image("racing-games-2.jpg");
    @include basicBackground;
  }
}

.animation-games {
  @include e(image) {
    aspect-ratio: 1.35 / 1;
  }
  & > *:nth-child(1) &__image {
    background: image("racing-games-1.jpg");
    @include basicBackground {
      aspect-ratio: 1.7 / 1;
    }
  }

  & > *:nth-child(2) &__image {
    background: image("animation-games-1.jpg");
    @include basicBackground;
  }

  & > *:nth-child(3) &__image {
    background: image("animation-games-2.jpg");
    @include basicBackground;
  }
}

.action-games {
  @include e(image) {
    aspect-ratio: 0.77 / 1;
    filter: brightness(50%);
  }
  & > *:nth-child(1) &__image {
    background: image("news-3-bg.jpg");
    @include basicBackground;
  }

  & > *:nth-child(2) &__image {
    background: image("news-2-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(3) &__image {
    background: image("action-3.jpg");
    @include basicBackground;
  }
  & > *:nth-child(4) &__image {
    background: image("featured-soldier3.jpg");
    @include basicBackground;
  }
  & > *:nth-child(5) &__image {
    background: image("news-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(6) &__image {
    background: image("news-4-bg.jpg");
    @include basicBackground;
  }
}

.latest-games {
  @include e(image) {
    aspect-ratio: 1.17 / 1;
  }
  & > *:nth-child(1) &__image {
    background: image("news-3-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(2) &__image {
    background: image("news-2-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(3) &__image {
    background: image("action-3.jpg");
    @include basicBackground;
  }
}

.recent-games {
  @include e(image) {
    aspect-ratio: 1 / 1;
    width: 100%;
  }
  & > *:nth-child(1) &__image {
    background: image("news-3-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(2) &__image {
    background: image("news-2-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(3) &__image {
    background: image("action-3.jpg");
    @include basicBackground;
  }
  & > *:nth-child(4) &__image {
    background: image("featured-soldier3.jpg");
    @include basicBackground;
  }
}

.popular-games {
  @include e(image) {
    aspect-ratio: 1 / 1;
    width: 100%;
  }
  & > *:nth-child(1) &__image {
    background: image("news-4-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(2) &__image {
    background: image("news-2-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(3) &__image {
    background: image("news-3-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(4) &__image {
    background: image("featured-soldier3.jpg");
    @include basicBackground;
  }
}

.most-viewed-games {
  @include e(image) {
    aspect-ratio: 1.17 / 1;
  }
  & > *:nth-child(1) &__image {
    background: image("animation-games-2.jpg");
    @include basicBackground;
    aspect-ratio: 1.35 / 1;
  }
  & > *:nth-child(2) &__image {
    background: image("animation-games-1.jpg");
    @include basicBackground;
  }
  & > *:nth-child(3) &__image {
    background: image("tanks-2.jpg");
    @include basicBackground;
  }
}

@each $i in $hoverItems {
  #{$i} .post:hover #{$i + "__image"},
  #{$i} .post__main:hover #{$i + "__image"},
  #{$i} .post-small:hover #{$i + "__image"} {
    transform: scale3d(1.1, 1.1, 1);
    transition: all 0.5s ease;
  }
}

.animation-games .posts__container:hover .animation-games__image,
.racing-games .posts__container:hover .racing-games__image {
  transform: scale3d(1.1, 1.1, 1);
  transition: all 0.5s ease;
}

@each $i in $hoverItemsWithShadow {
  #{$i} .post:hover #{$i + "__image"},
  #{$i} .post__main:hover #{$i + "__image"},
  #{$i} .post__main:hover .post__main-background,
  #{$i} .post-small:hover #{$i + "__image"} {
    transform: scale3d(1.1, 1.1, 1);
    transition: all 0.5s ease;
  }
}
