.slider-control-prev,
.slider-control-next {
  height: fit-content;
  position: absolute;
  @include positioning(50%);
  transform: translateY(-50%);
  transition: all 0.3s ease-out;
  @include getCssVar(background-color, light-white);
  width: rem($newsButtonSize);
  height: rem($newsButtonSize);
  border-radius: 50%;
  z-index: 1;
  @include getCssVar(color, white);
  cursor: pointer;
}

.slider-control-prev:hover,
.slider-control-next:hover {
  @include getCssVar(background-color, red);
}

.slider-control-next {
  right: 0;
}

.slider-control-prev {
  left: 0;
}
