.post__main {
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: rem($postsMainRowGap);
  overflow: hidden;
  &,
  & a {
    @include getCssVar(color, white);
    text-decoration: none;
  }
  @include m(tag) {
    @include setTextProperties(sans, regular, $postsMainTagFontSize);
    z-index: 2;
    & > a {
      @include getCssVar(background-color, blue);
      clip-path: polygon(100% 0%, 90% 50%, 100% 100%, 0 100%, 0% 50%, 0 0);
      padding: rem($postsMainTagTopBotP) rem($postsMainTagRightP)
        rem($postsMainTagTopBotP) rem($postsMainTagLeftP);
    }
  }
  @include m(date) {
    @include setTextProperties(sans, regular, $postsMainDateFontSize);
    z-index: 2;
  }
  @include m(title) {
    @include setTextProperties(rubik, medium, $postsMainTitleFontSize, 1.4);
    z-index: 2;
    margin: unset;
  }
  @include m(description) {
    @include setTextProperties(sans, regular, $baseFontSize);
    z-index: 2;
    margin: unset;
  }
  @include m(info) {
    @include setFlex(row, flex-start, center);
    column-gap: rem($postsMainColGap);
    flex-wrap: wrap;
    @include unsetAll((padding, margin));
    z-index: 2;
    list-style: none;
    @include setTextProperties(sans, regular, $postsMainInfoFontSize);
    & > li a {
      @include getCssVar(font-weight, medium);
      transition: all ease 0.31s;
    }
  }
  & i {
    @include setTextProperties(awesome, regular, $postsMainInfoIconSize);
    margin-right: rem($postsMainIconRightM);
    @include getCssVar(color, red);
  }
  & li a:hover {
    @include getCssVar(color, red);
  }
}
