.header__navbar {
  display: grid;
  grid-template-columns: repeat(3, auto);
  place-content: space-between;
  position: relative;

  @include m(toggler) {
    margin: auto;
    width: rem($headerMobileTogglerWidth);
    &,
    &:focus {
      @include unsetAll((border, box-shadow));
    }

    @include m(bar) {
      @include getCssVar(background, black);
      height: rem($headerMenuBarHeight);
      width: rem($headerMenuBarWidth);
      display: block;
      margin-bottom: rem($headerMenuBarBotM);
      transition: all ease 0.31s;
      &:nth-of-type(2),
      &:nth-of-type(3) {
        width: rem($headerMenuBarWidth * 0.8);
      }
    }

    @include m(active) {
      & > .header__navbar-toggler-bar {
        opacity: 0;
        &:nth-child(1),
        &:nth-last-child(1) {
          position: absolute;
          top: 50%;
          opacity: 1;
        }
        &:nth-child(1) {
          transform: rotate(45deg);
        }
        &:nth-last-child(1) {
          transform: rotate(-45deg);
        }
      }
    }
  }
}

.navbar__menu {
  position: absolute;
  @include getCssVar(background-color, white);
  @include positioning(126%, rem(-$headerSidesP));
  margin: 0;
  z-index: 10;
  width: 100vw;
  transition: all linear 0.2s;
  transform: scaleY(0);
  transform-origin: top;
}

.navbar__menu,
.menu__sub-menu-list {
  & a {
    position: relative;
    &::before {
      position: absolute;
      content: "";
      width: rem($headerMenuBeforeW);
      @include positioning(50%, rem($headerMenuBeforeLeftPos));
      transform: translateY(-50%);
      background: #6c6c6c;
      visibility: hidden;
      transition: all ease 0.31s;
      @include resetAllToZero((opacity, margin, height));
    }

    &:hover::before {
      @include getCssVar(background, red);
      height: rem($headerMenuBeforeHoverH);
      opacity: 1;
      visibility: visible;
      transition: all ease 0.31s;
    }
  }
}

.menu {
  @include e(list) {
    list-style: none;
    @include unsetAll((margin, padding));
    @include m(item) {
      position: relative;
      text-transform: uppercase;
      border-bottom: $baseBorder solid #e9e9e9;
      &:nth-of-type(1) {
        border-top: $baseBorder solid #e9e9e9;
      }
      padding: rem($headerMenuItemTopP) 0 rem($headerMenuItemBotP)
        rem($headerMenuItemRight);

      @include m(link) {
        text-decoration: none;
        color: inherit;
        display: block;
        margin: unset;
        position: relative;
      }
    }
  }

  @include e(sub-menu) {
    @include m(list) {
      list-style: none;
      padding: unset;
    }

    @include m(item) {
      padding: rem($headerMenuItemTopP) 0 rem($headerMenuItemBotP)
        rem($headerMenuItemRight);

      & > a,
      & > p {
        display: block;
        margin: unset;
        @include setTextProperties(rubik, medium, $headerMenuItemFontSize);
        @include getCssVar(color, black);
        text-decoration: none;
        position: relative;
      }
    }
  }
}

.show-mobile-menu {
  transform: scaleY(1);
}

.expand-menu {
  &::after {
    content: "\002b";
    position: absolute;
    display: grid;
    place-content: center;
    @include positioning(5%, null, null, rem($headerMenuAfterRightPos));
    @include setTextProperties(awesome, bold, $headerMenuAfterFontSize);
  }
}

.hide-menu {
  &::after {
    content: "\f068";
    position: absolute;
    display: grid;
    place-content: center;
    @include positioning(5%, null, null, rem($headerMenuAfterRightPos));
    @include setTextProperties(awesome, bold, $headerMenuAfterFontSize);
  }
}
