.posts__heading {
  position: relative;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(2, auto);
  z-index: 1;
  margin-bottom: rem($postsHeadingsBotM);
  min-height: rem($postsHeadingsMinH);
  @include getCssVar(background, red);

  @include m(title) {
    @include setTextProperties(rubik, semi-bold, $postsHeadingFontSize, 1.4);
    @include getCssVar(color, white);
    margin: 0;
    padding-left: rem($postsHeadingTitleLeftP);
  }

  @include m(cta) {
    @include setTextProperties(sans, regular, $baseFontSize);
    @include getCssVar(color, white);
    text-decoration: none;
    @include setFlex(row, flex-end, center);
    padding: rem($postsHeadingCtaBotTopP) rem($postsHeadingCtaSidesP);
    & > span {
      margin-right: rem($postsHeadingCtaTextRightM);
      transform: translateX($postsHeadingCtaTextTransform);
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease;
    }

    &:hover > span {
      transform: translateX(0);
      opacity: 1;
      visibility: visible;
    }
  }

  &::after {
    @include beforeAfterConstructorSquare(rem($postsHeadingCtaAfterSize)) {
      @include getCssVar(background, red);
      @include positioning(
        null,
        rem($postsHeadingCtaAfterLeftPos),
        rem($postsHeadingCtaAfterBotPos)
      );
      transform: rotatez(-45deg);
      z-index: -1;
    }
  }
}
