.slider-item {
  padding: rem($newsTopP) 0 rem($newsBotP);

  @include e(inner) {
    max-width: $newsMaxW;
    margin-left: rem($newsLeftM - $newsInnerSidesP);
    min-height: unset;

    @include m(date) {
      margin-bottom: rem($newsInnerDateBotM);
    }
  }
}

.inner__info {
  margin-bottom: rem($newsInnerBotM);
}
