.action-games {
  display: grid;
  grid-auto-rows: auto;
  grid-row-gap: rem($postsArticleActionGaps);
  margin-bottom: rem($postsArticleActionBotM);
  & > .post__main {
    aspect-ratio: 0.77 / 1;
  }
  @include e(content) {
    position: absolute;
    bottom: 0;
    padding: rem($postsArticleActionContentTopBotP) rem($postsArticleActionContentSidesP);
    display: grid;
    grid-auto-columns: auto;
    grid-column-gap: rem($postsArticleActionContentColGap);

    & > .post__main-title {
      font-size: rem($postsArticleActionContentTitleFontSize);
    }
  }
}
