.post-small {
  grid-template-rows: auto max-content;
  &:not(:nth-last-of-type(1)) {
    padding-bottom: rem($postsSmallBotP);
    margin-bottom: rem($postsSmallBotM);
    border-bottom: $baseBorder solid getInlineCssVar(light-grey);
  }

  @include e(title) {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    white-space: normal;
  }
}
