.post-small {
  display: grid;
  grid-template-columns: minmax(auto, $postsSmallMaxImage) 1fr;
  grid-template-rows: repeat(2, auto);
  grid-column-gap: rem($postsSmallColGap);
  &:not(:nth-last-of-type(1)) {
    padding-bottom: rem($postsSmallBotP);
    margin-bottom: rem($postsSmallBotM);
    border-bottom: $baseBorder solid getInlineCssVar(light-grey);
  }
  @include e(image-container) {
    overflow: hidden;
    grid-area: 1 / 1 / 3 / 2;
    align-self: center;
  }
  @include e(date) {
    grid-area: 1 / 2 / 2 / 3;
  }

  @include e(title) {
    grid-area: 2 / 2 / 3 / 3;
  }
}
