.header-buttons__search {
  align-self: center;
  @include getCssVar(color, black);
  & > a {
    color: inherit;
  }
}

.navbar__search {
  visibility: hidden;
  transform: scaleY(0);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  transition: all ease 0.31s;
  z-index: 20;
  transform-origin: top;
  @include getCssVar(background-color, light-grey);
  @include m(open) {
    visibility: visible;
    transform: scaleY(1);
  }

  @include m(container) {
    position: absolute;
    width: 100%;
    top: 50%;
    text-align: center;
  }

  @include m(input) {
    width: 53%;
    border-bottom: $baseBorder solid getInlineCssVar(red);
    padding: 20px 35px 20px 0;
    @include setTextProperties(rubik, regular, 24px);
    font-size: clamp(1rem, rem(24px), 2vw);
    @include getCssVar(color, 50white);
  }

  @include m(but) {
    @include setTextProperties(awesome, black-bold, 24px, 1);
    @include getCssVar(color, white);
    transition: all ease 0.31s;
    cursor: pointer;
    &:hover {
      @include getCssVar(color, red);
    }
  }

  @include m(close) {
    @include getCssVar(color, 50white);
    @include setTextProperties(rubik, light, 60px);
    position: absolute;
    @include positioning(5%, null, null, 5%);
    cursor: pointer;
    transition: all ease 0.31s;

    &:hover {
      transform: scale3d(1.1, 1.1, 1);
      @include getCssVar(color, white);
    }
  }
}
