.footer__address {
  & > * {
    margin: unset;
  }
  & > *:not(:nth-last-of-type(1)) {
    margin-bottom: rem($footerAddressChildrenMarginB);
  }
  font-style: normal;
  line-height: 1.6;
  @include m(title) {
    @include getCssVar(color, white);
    @include setTextProperties(sans, medium, $baseFontSize);
  }

  @include m(contact) {
    transition: all 0.4s ease;
    @include getCssVar(color, white);
    @include setTextProperties(sans, regular, $baseFontSize);
    text-decoration: none;
  }

  & a:hover {
    @include getCssVar(color, red);
  }
}
