body {
  line-height: 1.9;
  @include getCssVar(background-color, background-black);
  overflow-x: hidden;
}

.topheader,
.desktop {
  display: none !important;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.tp-sm {
  top: 1rem;
}

.lp-sm {
  left: 1rem;
}

.lp-smb {
  left: 1.5rem;
}

.active {
  color: var(--gamxo-red) !important;
}

.collapse {
  display: none;
}

.show {
  display: block;
}

.justify-content-start {
  justify-content: start;
}

.button-style-1 {
  padding: rem(12px) rem(25px) rem(8px);
  margin-top: rem(20px);
  border: 2px solid getInlineCssVar(red);
  @include setTextProperties(sans, medium, 14px);
  text-transform: uppercase;
  display: inline-flex;
  align-items: center;
  z-index: 1;
  & i {
    opacity: 0;
    margin-left: rem(-20px);
    visibility: hidden;
    transition: all 0.4s ease;
    @include getCssVar(font-weight, semi-bold);
    @include getCssVar(color, white);
    margin-right: unset;
  }

  &::before {
    opacity: 0;
    visibility: hidden;
    background-size: 300% 100%;
    background-image: linear-gradient(
      to right,
      getInlineCssVar(red),
      getInlineCssVar(orange),
      getInlineCssVar(red),
      getInlineCssVar(orange)
    );
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    content: "";
    height: 100%;
    width: 100%;
    color: white;
    transition: all 0.4s ease;
  }

  &:hover {
    & i {
      opacity: 1;
      margin-left: rem(10px);
      visibility: visible;
      transition: all 0.4s ease;
    }
    &::before {
      background-position: 40% 0;
      visibility: visible;
      opacity: 1;
    }
  }
}

.button-style-2 {
  position: relative;
  display: grid;
  place-items: center;
  z-index: 1;
  margin-bottom: rem(20px);
  min-height: rem(50px);
  transition: all 0.5s ease-out;

  background-color: transparent;

  @include e(cta) {
    @include setTextProperties(sans, regular, $baseFontSize);
    @include getCssVar(color, white);
    width: 100%;
    height: 100%;
    text-decoration: none;
    display: grid;
    place-items: center;
    padding: rem(7px) rem(20px);
    transition: all 0.5s ease-out;
    border: rem($baseBorder) solid getInlineCssVar(light-grey);

    @include m(title) {
      @include setTextProperties(rubik, semi-bold, 18px, 1.4);
      @include getCssVar(color, white);
      @include unsetAll((padding, margin));
    }

    &:hover {
      border: rem($baseBorder) solid getInlineCssVar(red);
      @include getCssVar(background-color, red);
    }
  }

  &::after {
    @include beforeAfterConstructorSquare(rem(15px)) {
      @include getCssVar(background, red);
      @include positioning(null, 46%, rem(5px));
      transform: rotatez(-45deg);
      transition: all 0.5s ease-out;
      z-index: -1;
      opacity: 0;
      visibility: hidden;
    }
  }

  @include m(is-active) {
    @include getCssVar(background, red);
    transition: all 0.5s ease-out;
    .button-style-2__cta {
      border: rem($baseBorder) solid getInlineCssVar(red);
    }

    &::after {
      @include beforeAfterConstructorSquare(rem(15px)) {
        @include positioning(null, 46%, rem(-7px));
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.scroll-button {
  position: fixed;
  width: 37px;
  aspect-ratio: 1 / 1;
  @include positioning(null, null, 1%, 2%);
  @include getCssVar(stroke, red);
  stroke-width: 4;
  box-sizing: border-box;
  cursor: pointer;

  opacity: 0;
  visibility: hidden;
  transform: translateY(0.75rem);
  transition: all 0.2s linear;
  &::after {
    @include beforeAfterConstructorSquare(auto) {
      content: "\f062";
      @include getCssVar(color, red);
      @include setTextProperties(awesome, black-bold, 14px, 36px);
      @include positioning(0, 36%);
      transition: all 0.2s linear;
    }
  }
  @include m(active) {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
