@import "./vars";
@import "./heading";
@import "./control-group";
@import "./articles/index";
@import "./general-post/index";

.posts {
  display: grid;
  grid-template-columns: 0.3845fr 0.3845fr 0.231fr;
  grid-auto-rows: auto;
  column-gap: rem($postsColGap);
  padding: rem($postsFeaturedPs);

  @include e(container) {
    grid-template-columns: 0.595fr 0.405fr;
  }
}
