@import "./vars";
@import "./socials-bg";

.social-icon {
  display: grid;
  width: rem($bigIconW);
  height: rem($bigIconH);
  place-items: center;
  @include setTextProperties(awesome, regular, $socialsBigIconSize);
  color: #fff;
  transition: all 0.5s ease;
}

.footer .social-icon {
  list-style: none;
  width: 100%;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(5, auto);
  column-gap: rem($smallIconGaps);
  justify-content: left;
  @include unsetAll((padding, margin));
  @include m(item) {
    width: rem($smallIconW);
    height: rem($smallIcowH);
    @include getCssVar(background-color, light-grey);
    display: grid;
    place-items: center;
    transition: all 0.5s ease;
    & > a {
      @include getCssVar(color, white);
      @include setTextProperties(sans, regular, $baseFontSize);
    }

    &:hover {
      @include getCssVar(background-color, red);
    }
  }
}
