:root {
  @include setCssVar(font-size, $baseFontSize);

  @each $font, $value in $fonts {
    @include setCssVar($font, $value);
  }

  @each $weight, $value in $weights {
    @include setCssVar($weight, $value);
  }

  @each $color, $value in $colors {
    @include setCssVar($color, $value);
  }

  @each $color, $value in $rgba {
    @include setCssVar($color, $value);
  }
}
