@import "./vars";
@import "./headings";
@import "./footer-categories";
@import "./footer-posts";
@import "./footer-address";
@import "./footer-form";

.footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(7, auto);
  padding: rem($footerTopP) rem($footerSidesP) rem($footerBotP);
  row-gap: rem($footerRowGap);
  column-gap: rem($footerColGap);

  @include e(image) {
    grid-area: 1 / 1 / 2 / 2;
  }

  @include e(description) {
    grid-area: 2 / 1 / 3 / 2;
    margin: unset;
  }

  & .social-icon {
    grid-area: 3 / 1 / 4 / 2;
  }

  @include e(copyrights) {
    grid-area: 7 / 1 / 8 / 3;
    width: 100vw;
    justify-self: center;
    text-align: center;
  }
}
