.email-subs {
  @include getCssVar(background-color, red);
  padding: rem(40px) rem(34px);

  display: grid;
  place-items: center;
  grid-auto-rows: auto;
  row-gap: rem(20px);

  @include e(content) {
    @include getCssVar(color, white);
    display: grid;
    grid-auto-rows: auto;
    row-gap: rem(3px);
    place-items: center;
    @include m(title) {
      @include setTextProperties(rubik, regular, 22px, 1.5);
      margin: unset;
      text-align: center;
    }
    @include m(subtitle) {
      @include setTextProperties(sans, regular, 14px, 1.5);
      margin-bottom: rem(17px);
    }
    &:after {
      @include beforeAfterConstructorSquare(auto) {
        content: image("subscribe-figure.png");
        position: relative;
      }
    }
  }

  @include e(input) {
    display: grid;
    place-items: center;
    grid-auto-rows: auto;
    row-gap: rem(5px);
    width: 100%;
    @include m(email) {
      @include getCssVar(background-color, white);
      border: rem($baseBorder) solid getInlineCssVar(white);
      padding: rem(6px) rem(12px);
      min-width: 100%;
      max-width: 100%;
      text-align: center;
      @include setTextProperties(sans, regular, $baseFontSize, 1.9);
    }

    @include m(submit) {
      @include getCssVar(background-color, white);
      border: rem($baseBorder) solid getInlineCssVar(white);
      padding: rem(9px) rem(20px);
      @include setTextProperties(sans, regular, $baseFontSize, 1.9);
      min-width: 100%;
      max-width: 100%;
      transition: all 0.4s ease;
      cursor: pointer;
      &:hover {
        @include getCssVar(background-color, red);
        @include getCssVar(color, white);
      }
    }
  }
}
