@import "./vars";
@import "./headings";
@import "./footer-categories";
@import "./footer-posts";
@import "./footer-images";
@import "./footer-address";
@import "./footer-form";

.footer {
  @include getCssVar(background-color, light-black);
  padding: rem($footerTopP) rem($footerSidesP) rem($footerBotP);

  @include e(description) {
    @include getCssVar(color, grey);
    @include setTextProperties(rubik, regular, $baseFontSize);
  }

  @include e(copyrights) {
    border-top: rem($baseBorder) solid getInlineCssVar(nero);
    padding-top: rem($footerCopyrightsTopP);
    margin-top: rem($footerCopyrightsTopM);
    @include setTextProperties(sans, regular, $footerCopyrightsFontSize);
    @include getCssVar(color, grey);

    & a {
      color: inherit;
      text-decoration: none;
      transition: all ease 0.31s;
      &:hover {
        @include getCssVar(color, white);
      }
    }
  }
}
