.footer__categories {
  list-style: none;
  padding: unset;
  display: grid;
  grid-auto-rows: auto;
  row-gap: rem($footerCategoriesRowGap);

  @include m(container) {
    text-decoration: none;
    height: fit-content;
    display: block;
    overflow: hidden;

    &:hover {
      & > .footer__categories-num {
        @include getCssVar(background-color, red);
      }
      & > .footer__categories-title {
        left: rem($footerCategoriesTitleLeftPosHov);
        &::before {
          left: rem($footerCategoriesChevronLeftPosHov);
        }
      }
    }
  }

  @include m(title) {
    transition: all 0.3s ease-in-out;
    @include setTextProperties(rubik, medium, $baseFontSize, 1.4);
    @include getCssVar(color, grey);
    position: absolute;
    @include positioning(30%, rem($footerCategoriesTitleLeftPos));
    letter-spacing: 1.6px;

    &::before {
      @include beforeAfterConstructorSquare(auto) {
        content: "\f054";
        @include setTextProperties(awesome, semi-bold, $footerCategoriesTitleFontSize);
        transition: all 0.4s ease;
        @include getCssVar(color, red);
        @include positioning(rem($baseBorder), rem($footerCategoriesChevronLeftPos));
      }
    }
  }

  @include m(num) {
    transition: all 0.3s ease-in-out;
    @include setTextProperties(rubik, regular, $baseFontSize, 1.4);
    @include getCssVar(color, grey);
    position: absolute;
    width: rem($footerCategoriesNumWidth);
    aspect-ratio: 1 / 1;
    display: grid;
    place-items: center;
    @include positioning(15%, null, null, 0);
    @include getCssVar(background-color, 50grey);
  }
}
