.news-section .slider-item__background {
  position: absolute;
  top: 0;
  display: block;
  height: 100%;
  width: 100%;
  filter: brightness($newsImageBrightness);

  z-index: 1;
}

.news-section .slider-item:nth-of-type(1) > .slider-item__background {
  background: image("news-bg.jpg");
  @include basicBackground(center) {
    transition: all 6s ease;
    transition-delay: 1s;
  }
}

.news-section .slider-item:nth-of-type(2) > .slider-item__background {
  background: image("news-2-bg.jpg");
