.post {
  display: grid;
  grid-auto-rows: auto;
  row-gap: rem($postsRegularGaps);
  @include e(image-container) {
    overflow: hidden;
    display: block;
  }
  @include e(description) {
    @include getCssVar(background-color, 50black);
    display: grid;
    grid-template-rows: repeat(2, auto);
    grid-row-gap: rem($postsRegularDescRowGap);
    padding: rem($postsRegularPs);
    @include m(date) {
      @include getCssVar(color, gray88);
      @include setTextProperties(sans, regular, $postsRegularDateFontSize);
      grid-area: 1 / 1 / 2 /2;
      & > i {
        @include setTextProperties(awesome, regular, $postsRegularDateIconSize);
        @include getCssVar(color, red);
        margin-right: $postsRegularIcomRightM;
      }
    }

    @include m(title) {
      @include setTextProperties(rubik, medium, $postsRegularTitleFontSize, 1.4);
      @include getCssVar(color, white);
      grid-area: 2 / 1 / 3 / 2;
      margin: unset;
      & > a {
        text-decoration: unset;
        color: inherit;
      }
    }
  }
}

@import "./post-small";
@import "./post-main";
