.latest-games {
  & .post {
    @include e(main) {
      grid-template-columns: $postsLatestImageMaxImageSize 1fr;
      column-gap: rem($postsLatestColGap);
    }
    @include e(main-tag) {
      margin-top: unset;
      margin-bottom: rem($postsArticleLatestTagBotM);
    }
    @include e(image-container) {
      margin-bottom: unset;
      grid-area: 1 / 1 / 6 / 2;
    }
  }
}
