.footer__posts {
  @include m(image) {
    aspect-ratio: 1 / 1;
  }
  & > *:nth-child(1) &-image {
    background: image("news-3-bg.jpg");
    @include basicBackground;
  }
  & > *:nth-child(2) &-image {
    background: image("news-2-bg.jpg");
    @include basicBackground;
  }
}

.footer__categories {
  @include m(image) {
    aspect-ratio: 1 / 0.18579;
    filter: brightness(50%);
  }
  & > *:nth-child(1) &-image {
    background: image("animation-games-2.jpg");
    @include basicBackground {
      background-position: center;
    }
  }

  & > *:nth-child(2) &-image {
    background: image("animation-games-1.jpg");
    @include basicBackground {
      background-position: center;
    }
  }

  & > *:nth-child(3) &-image {
    background: image("featured-main-post.jpg");
    @include basicBackground {
      background-position: center;
    }
  }

  & > *:nth-child(4) &-image {
    background: image("tanks-2.jpg");
    @include basicBackground {
      background-position: center;
    }
  }
}

a:hover > .footer__posts-image {
  transform: scale3d(1.1, 1.1, 1);
  transition: all 0.5s ease;
}

a:hover > .footer__categories-image {
  transform: scale3d(1.1, 1.1, 1);
  -webkit-box-shadow: inset 0px -8px 208px -44px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: inset 0px -8px 208px -44px rgba(0, 0, 0, 0.6);
  box-shadow: inset 0px -8px 208px -44px rgba(0, 0, 0, 0.6);
}
